// ==========================================================================
// LAWYERS
// ==========================================================================

@include new("lawyer") {

    @include has("header") {
        color: $white;
        background-color: $main-color;

        > div:first-child {
            padding: 6rem 10vw;
        }

        > div:last-child {
            padding: 0 0;
        }
    }

    @include has("name") {
        @include fontFull(64px, 64px);
        line-height: 1;
        margin-bottom: 0;
    }

    @include has("position") {
        @include fontFull(64px, 64px);
        margin-bottom: 2rem;
        line-height: 1;
        /* >>> force line break hack (maybe delete ¿?)
        https://stackoverflow.com/questions/4212909/can-css-force-a-line-break-after-each-word-in-an-element */
        word-spacing: 100vw;
        color: $complementary-color;
    }

    @include has("scope") {
        margin-bottom: 2rem;

        .related__services__link {
            @include classic-link($complementary-color, $white, $border: false);
        }
    }

    @include has("contact") {
        margin-bottom: 2rem;

        > * {
            @include fontFull(16px, 20px);
        }

        [data-address] {
            text-transform: uppercase;
        }

        [data-telephone] {
            position: relative;
            padding-left: 1em;

            &:after {
                content: "T.";
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        [data-email] {
            a {
                @include classic-link($white, $white);
            }
        }
    }

    @include has("details") {

        > * {
            @include fontFull(16px, 20px);
        }

        [data-langs] {
            opacity: 0.5;
        }

        [data-vcard] {
            margin-top: 0.2rem;
            text-transform: uppercase;

            a {
                @include classic-link($complementary-color, $complementary-color);
            }
        }
    }

    .action__links {
        position: relative;
        bottom: -4.5rem;
        margin-left: -0.5rem;
    }

    @include has("media") {
        @include reset-figure;
        width: 100%;
        height: 100%;

        @include has("photo") {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    //
    // Main sections
    //

    @include has("resume") {

        p:first-child {
            margin-bottom: 2.4rem;
            @include fontFull(32px, 42px);
        }

        p {
            @include fontFull(24px, 30px);
        }

        a {
            @include fancy-link($complementary-color);
        }

        .inline__quote {
            float: left;
            width: 30%;
            margin: 0 2rem 0 0;
        }
    }

    @include has("section") {
        display: flex;
        position: relative;
        padding: 2rem 10vw;

        &:before {
            content: '';
            position: absolute;
            top: 0;
            width: calc(100% - 20vw);
            height: 2px;
            background-color: rgba($black, 0.5);
        }

        .block__title {
            flex: 0 0 30%;
            margin-right: 2rem;
        }

        .block__content {}

        @include when("block") {
            display: block;

            .block__title {
                margin-bottom: 2rem;
            }
        }
    }

    @include has("experience") {}
    @include has("mentions") {}
    @include has("education") {}
}
